<template>
    <v-container
    >
        <v-row>
            <v-col>
                <v-card>
                    <v-card-title
                        class="transitions align-baseline justify-center"
                    >   
                        <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    block
                                    text
                                    color="info"
                                    @click="changeLoginOption = !changeLoginOption"
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                {{ changeLoginOption ? "Регистрация": "Логин"}}
                                </v-btn>
                            </template>
                            <span>Сменить на {{changeLoginOption ? "логин": "регистрация"}}</span>
                        </v-tooltip>
                    </v-card-title>
                    <v-card-actions
                        class="justify-sm-center"
                    >
                        <v-form
                            v-model="userForm.valid"  
                            ref="userForm"
                            lazy-validation  
                        >
                            <v-fade-transition
                                tag="span"
                                appear
                            >
                                <v-container>
                                    <v-row
                                        justify="center"
                                    >
                                        <v-col
                                            cols="12"
                                            sm="12"
                                            md="9"
                                            lg="8"
                                        >
                                            <v-text-field
                                                hide-details
                                                solo
                                                prepend-inner-icon="mdi-account-edit"
                                                :label="changeLoginOption ? ' Введите имя': 'Придумайте имя'"
                                            ></v-text-field>
                                        </v-col> 
                                        <v-col
                                            cols="12"
                                            sm="12"
                                            md="9"
                                            lg="8"
                                        >
                                            <v-text-field
                                                hide-details
                                                solo
                                                prepend-inner-icon="mdi-pencil-lock"
                                                :label="changeLoginOption ? ' Введите пароль': 'Придумайте пароль'"
                                            ></v-text-field>
                                        </v-col>
                                        <v-col
                                            v-if="changeLoginOption"
                                            cols="12"
                                            sm="12"
                                            md="9"
                                            lg="8"
                                        >
                                            <v-text-field
                                                solo
                                                hide-details
                                                label="Придумайте почту"
                                                prepend-inner-icon="mdi-email"
                                            ></v-text-field>
                                        </v-col>
                                        <v-col
                                            cols="12"
                                            sm="12"
                                            md="9"
                                            lg="8"
                                        >
                                            <v-btn
                                                color="primary"
                                                block
                                            >{{ changeLoginOption ? "Войти": "Зарегестрироваться" }}</v-btn>
                                        </v-col>
                                        <!-- Сделать при валидации редирект с запоминанием даты и дату пушать в роут -->
                                    </v-row>
                                </v-container>
                            </v-fade-transition>
                        </v-form>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
    export default {
        name: "Login",
        data: () => ({
            changeLoginOption: false,
            userForm: {
                valid: true,
                rules: {
                    require: [v => !!v || "Поле обязательно к заполенению!"]
                }
            },
        }),
        methods: {
            valid() {
                return this.$refs.userForm.validate();
            }
        },
        mounted() {
            return console.log(!!this.$route.params.userLogin && this.$route.params.userEmail);
        }
        
    }
</script>
<style scoped lang="scss">
    .d {
        &-contents {
            display: contents;
        }
    }
    .container {
        align-items: center;
        display: flex;
        height: 100%;
    }
</style>